import {
		ajax,
	} from '@/static/js/base.js'
	import api from '@/static/js/api.js'
export default {
	data() {
		return {
			formNode:{
				loan_name: '',
				type: '',
				repayment: '',
				mobile: '',
				company: '',
				contacts: '',
				amount_of_money: '',
				interest_rate: ''
			},
		};
	},
	methods: {
		inputValue(e){
			let formNode = this.formNode;
			let name = e.currentTarget.dataset.name;
			let value = e.detail.value;
			formNode[name] = value;
			this.formNode = formNode;
		},
		submit(){
			let formNode = this.formNode
			// album
			// return false
			if(formNode.loan_name == ''){
				uni.showToast({
					title: '请填写贷款名称',
					icon: 'none'
				})
				return false
			}
			if(formNode.type == ''){
				uni.showToast({
					title: '请填写行业类型',
					icon: 'none'
				})
				return false
			}
			if(formNode.repayment == ''){
				uni.showToast({
					title: '请填写还款方式',
					icon: 'none'
				})
				return false
			}
			if(formNode.interest_rate == ''){
				uni.showToast({
					title: '请填写贷款利率',
					icon: 'none'
				})
				return false
			}
			if(formNode.amount_of_money == ''){
				uni.showToast({
					title: '请填写借款额度',
					icon: 'none'
				})
				return false
			}
			if(formNode.contacts == ''){
				uni.showToast({
					title: '请填写联系人',
					icon: 'none'
				})
				return false
			}
			if(formNode.mobile == ''){
				uni.showToast({
					title: '请填写手机号',
					icon: 'none'
				})
				return false
			}
			if(formNode.mobile.length !='11'){
				uni.showToast({
					title: '请填写正确手机号',
					icon: 'none'
				})
				return false
			}
			if(formNode.company == ''){
				uni.showToast({
					title: '请填写单位',
					icon: 'none'
				})
				return false
			}
			ajax({
				url: api.addLoan,
				type: "POST",
				data: this.formNode,
			}).then(res => {
				if (res.status_code == "ok") {
					console.log(res)
					uni.showToast({
						title: res.message,
						icon:'none'
					})
					setTimeout( e =>{
						uni.navigateBack({
							delta:1
						})
					},2000)
					// this.info = res.data
				}else{
					uni.showToast({
						title: res.message,
						icon:'none'
					})
				}
			})
		}
	}
};
